.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes colors {
  from {
    background-color: black;
    border: 2px solid black;
  }
  to {
    background-color: #92d9d4;
    border: 4px solid black;
  }
}
@keyframes colors2 {
  from {
    color: black;
  }
  to {
    color: rgb(252, 76, 2);
  }
}
@keyframes scaleUp {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.5);
  }
}
@keyframes background {
  from {
    background-color: white;
    color: black;
  }
  to {
    background-color: #92d9d4;
    color: white;
  }
}
@keyframes scaleUp {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.5);
  }
}
