.app {
  text-align: center;
  background-image: url("../assets/images/bgBike.jpg");
  background-position: bottom;
  background-size: cover;
  height: 100vh;
  width: 100vw;
}
.header {
  height: 6vh;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__logo {
  background-image: url("../assets/images/Logo.png");
  background-size: contain;
  background-repeat: no-repeat;
  width: 20vw;
  height: 100%;
  margin-left: 2rem;
}
@media (max-width: 792px) {
  .header__logo {
    width: 50vw;
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header__logo {
    width: 50vw;
  }
}
.header .nav__element {
  padding: 0 26px;
}
.header .nav__element:last-child {
  padding-right: 4rem;
}
.header .nav__link {
  color: black;
  font-weight: 700;
  font-size: 1rem;
  text-transform: uppercase;
  padding-bottom: 25px;
  border-bottom: 3px solid transparent;
  letter-spacing: 0.1875rem;
  justify-content: center;
  align-items: center;
}
@media (max-width: 792px) {
  .header .nav__link {
    font-size: 1rem;
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header .nav__link {
    font-size: 1rem;
  }
}
.header .nav__link:hover {
  color: white;
  border-color: white;
}
@media (max-width: 792px) {
  .header .nav__link:hover {
    border: none;
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header .nav__link:hover {
    border: none;
  }
}
.header__nav {
  display: flex;
}
@media (max-width: 792px) {
  .header__nav {
    position: absolute;
    left: 0;
    top: 6vh;
    flex-direction: column;
    background-color: #aae9e1;
    width: 100%;
    display: none;
    height: 60vh;
    border-radius: 1rem;
    z-index: 5;
    justify-content: space-around;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  }
  .header__nav .nav__element {
    padding: 1rem;
  }
  .header__nav .nav__element:last-child {
    padding: 1rem;
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header__nav {
    position: absolute;
    left: 0;
    top: 6vh;
    flex-direction: column;
    background-color: #aae9e1;
    width: 100%;
    display: none;
    height: 60vh;
    border-radius: 1rem;
    z-index: 5;
    justify-content: space-around;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  }
  .header__nav .nav__element {
    padding: 1rem;
  }
  .header__nav .nav__element:last-child {
    padding: 1rem;
  }
  .header__nav .nav__link {
    display: block;
    padding: 14px 20px;
  }
}
.header .menu__btn {
  display: none;
  position: absolute;
  top: -9999px;
  left: -9999px;
  opacity: 0;
}
@media (max-width: 792px) {
  .header .menu__btn:checked ~ .header__nav {
    display: flex;
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header .menu__btn:checked ~ .header__nav {
    display: flex;
    transition: height 0.3s ease;
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(1) {
    transform: rotate(45deg) translate(2px, 5px);
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(2) {
    opacity: 0;
  }
  .header .menu__btn:checked ~ .menu__toggle span:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -5px);
  }
}
.header .menu__toggle {
  display: none;
}
.header .menu__toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: white;
  transition: 0.3s all ease-in-out;
}
.header .menu__toggle span:last-child {
  margin-bottom: 0;
}
@media (max-width: 792px) {
  .header .menu__toggle {
    display: block;
    margin-right: 1rem;
  }
}
@media (min-width: 793px) and (max-width: 1023px) {
  .header .menu__toggle {
    display: block;
    margin-right: 1rem;
  }
}

