#strava::before {
  content: "";
  background-image: linear-gradient(-117deg, transparent 96px, white 97px 115px, transparent 116px), linear-gradient(117deg, transparent 96px, white 97px 115px, transparent 116px);
  background-position: 64px 0px, -36px 0px;
  background-repeat: no-repeat;
  width: 100px;
  height: 86px;
  bottom: 82px;
  right: 66px;
  position: absolute;
  z-index: 1;
  animation: stravaLogo 3s ease-in-out infinite alternate;
}

#strava::after {
  content: "";
  background-image: linear-gradient(-117deg, transparent 10px, #ffb08b 11px 24px, transparent 25px), linear-gradient(117deg, transparent 10px, #ffb08b 11px 24px, transparent 25px);
  background-position: -28px 0px, 28px 0px;
  background-repeat: no-repeat;
  height: 56px;
  width: 57px;
  top: 107px;
  left: 82px;
  position: absolute;
  z-index: 1;
  animation: stravaLogo 3s ease-in-out infinite alternate;
